import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl
    }
  },
  methods: {
    // 跳转专题列表
    onSkipPageFn (item) {
      let url = ''
      switch (item.v_type) {
        case 0:
          url = `/pages/subject/subjectMusic/index?sid=${item.id}`
          break
        case 1:
          item['special_id'] = item.id
          url = `/pages/subject/subjectVideo/index?oJson=${encodeURIComponent(JSON.stringify(item))}`
          break
      }
      uni.navigateTo({
        url
      })
    }
  },
  props: {
    lists: {
      type: Array,
      default: []
    }
  }
}
